@import '../../style/App.scss';

.blogModal {
  display: flex;
  justify-content: left;
  align-items: left;
  width: 60%;
  min-width: 60%;
  // height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 200;
  margin: 0 20%;

  .backdrop {
    background-color: $black-color;
    opacity: 0.8;
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 300;
    display: block;
    cursor: pointer;
  }

  div {
    z-index: 400;

    .blog_show_modal {
      margin: 5px;
      background-color: $white-color;
      z-index: 500;
      padding: 30px;
      border-radius: 15px;
      display: flex;
      flex-direction: column;
      height: 800px;
      overflow-y: auto;
      overflow: auto;
      justify-content: left;
      align-items: left;

      // align-items: center;
      // justify-content: center;

      p {
        font-size: 1.5rem;
        color: #1E1E1E;
        font-weight: 800;

        &:nth-child(1) {
          font-size: 3rem;
          font-weight: 700;
          color: $main-color;
        }

        &:nth-child(2) {
          margin: 20px 0;
        }
      }

      .modal__btn {
        background-color: $main-color;
        padding: 10px 25px;
        border-radius: 10px;
        cursor: pointer;

        p {
          color: #1E1E1E;
          font-size: 1.4rem;
        }
      }
    }
  }


  div {
    z-index: 400;

    .error__modal {
      margin: 5px;
      background-color: $white-color;
      z-index: 500;
      padding: 30px;
      border-radius: 15px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      text-align: center;

      p {
        font-size: 1.5rem;
        color: #1E1E1E;
        font-weight: 800;

        &:nth-child(1) {
          font-size: 3rem;
          font-weight: 700;
          color: #F45C21;
          margin-bottom: 10px;
        }

        &:nth-child(2) {
          margin: 10px 0 0 0;
        }

        &:nth-child(3) {
          margin: 20px 0;
          font-size: 2rem;
          color: #1E1E1E;
          font-weight: 700;
        }
      }

      .modal__btn {
        background-color: $main-color;
        padding: 10px 25px;
        border-radius: 10px;
        cursor: pointer;

        p {
          margin: 0px;
          padding: 0px;
          color: #1E1E1E;
          font-size: 1.4rem;
        }
      }
    }
  }
}


@media only screen and (max-width: 1200px) {
  .blogModal {
    display: flex;
    justify-content: left;
    align-items: left;
    width: 76%;
    // height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 200;
    // text-align: center;
    margin: 0 12%;
  }
}