@import '../../style/App.scss';
  
.project__box {
  position: relative;
  max-width: 370px;
  margin: 15px auto;
  cursor: pointer;

  img {
    width: 100%;
    height: auto;
  }

  .project__hover-info {
    background-color: rgba($main-color, 0.8);
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    transition: 0.3s ease;
    
    p {
      text-transform: uppercase;
    }
  }
  .project__hover-info:hover {
    opacity: 1;
  }
}